/*vision*/
@font-face {
  font-family: 'vision';
  src: url("/assets/fonts/Vision-Regular.otf") format("otf");
  src: url("/assets/fonts/Vision-Regular.ttf") format("TrueType");
  font-weight: 400;
}

@font-face {
  font-family: 'vision';
  src: url("/assets/fonts/Vision-Bold.otf") format("otf");
  src: url("/assets/fonts/Vision-Bold.ttf") format("TrueType");
  font-weight: 800;
}

@font-face {
  font-family: 'vision';
  src: url("/assets/fonts/Vision-BoldItalic.otf") format("otf");
  src: url("/assets/fonts/Vision-BoldItalic.ttf") format("TrueType");
  font-weight: 800;
  font-style: italic;
}

/*splandor*/
@font-face {
  font-family: 'splandor';
  src: url("/assets/fonts/splandor.otf") format("otf");
  src: url("/assets/fonts/splandor.ttf") format("TrueType");
  font-weight: 400;
}

$font-main: 'vision', arial, sans-serif;
$font-title: 'splandor', arial, sans-serif;