@mixin xlarge{
  @media (min-width: media-rem(160)){@content}
}
@mixin large{
  @media (min-width: media-rem(128)){@content}
}
@mixin desktop{
  @media (min-width: media-rem(99.2)){@content}
}
@mixin mobile{
  @media (max-width: media-rem(76.799)){@content}
}
@mixin tablet{
  @media (min-width: media-rem(76.8)) and (max-width: media-rem(99.199)){@content}
}
@mixin not-large{
  @media (max-width: media-rem(127.999)){@content}
}
@mixin not-desktop{
  @media (max-width: media-rem(99.199)){@content}
}
@mixin not-mobile{
  @media (min-width: media-rem(76.8)){@content}
}
@mixin not-small-mobile{
  @media (min-width: media-rem(37.5)){@content}
}