body{
  .hero{
    .container{
      justify-content: center;
    }

    &-logo{
      width: 100%;

      &.white{
        display: none;
      }
    }

    @include desktop{
      .container{
        justify-content: initial;
      }
      background-image: url("/assets/images/hero_img.jpg");
      background-size: 100% auto;
      background-repeat: no-repeat;
      &-logo{
        &.white{
          display: block;
        }
        &.red{
          display: none;
        }
      }
    }

    @include large{
      height: 111vh;
      background-size: 100% auto;
      background-repeat: no-repeat;

      &-logo{
        margin-top: 12.5rem;
        width: 80%;
      }
    }
  }

  .presentation{

    .container{

      flex-direction: column-reverse;

      >div{
        width: 100%;

        &:nth-of-type(1) {

          margin-top: 4rem;

          >img{
            width: 100%;
          }

          @include desktop{
            margin-top: 0;
          }
        }
        @include desktop{
          &:nth-of-type(2){
            margin-left: 8rem;
          }
          width: 50%;
        }
      }

      @include desktop{
        flex-direction: initial;
      }
    }


    &-content{

      width: 100%;

      >div{

        display: flex;
        flex-direction: column;
        align-items: center;

        &:nth-of-type(1) {
          padding: 4rem 0;
        }

        >img{
          width: 4rem;
          margin-bottom: 2.8rem;

          @include desktop{
            margin-right: 3.9rem;
            width: 9.4rem;
            margin-bottom: 0;
          }
        }

        @include desktop{
          justify-content: center;
          flex-direction: initial;
          align-items: initial;
        }
      }

      *{
        text-align: center;

        @include desktop{
          text-align: initial;
        }
      }
    }
  }

  .teaser{

    &-content{

      background-color: $primary;
      border-radius: 1rem;
      width: 100%;
      height: 17.5rem;
      overflow: hidden;

      @include desktop{
        height: 52.3rem;
        margin: 0 19.5rem;
        border-radius: 3rem;
      }

      iframe{
        width: 100%;
        height: 100%;
      }
    }
  }

  .localisation{

    &-content{
      background-color: $secondary;
      width: 100%;
      display: flex;
      justify-content: flex-end;
      border-radius: $global-radius;
      overflow: hidden;
      position: relative;

      flex-direction: column-reverse;

      @include desktop{
        flex-direction: initial;
      }

      &-map{
        height: 100%;
        width: 100%;
        z-index: 0;

        iframe{
          width: 100%;
          height: 100%;
        }

        @include desktop{
          position: absolute;
          width: 66%;
          top: 0;
          left: 0;
        }
      }

      &-main{
        padding: 3.2rem 4rem;
        display: flex;
        flex-direction: column;
        background-color: $primary;
        border-radius: $global-radius $global-radius 0 0;
        position: relative;
        z-index: 10;
        align-items: center;

        @include desktop{
          width: 34%;
          border-radius: $global-radius;
          padding: 4.9rem 6.2rem;
          align-items: initial;
        }

        *{
          color: $white;
        }

        >div{
          padding: 1.6rem 0 2.9rem 0;
          display: flex;
          flex-direction: column;
          align-items: center;

          >img{
            margin-bottom: 1rem;

            @include desktop{
              margin-bottom: 0;
            }
          }

          @include desktop{
            flex-direction: initial;
            align-items: initial;
            padding: 3rem 0 4rem 0;
          }
        }
      }
    }
  }

  .gobelins{

    .container{
      flex-direction: column;
    }

    &-content{
      >div{
        &:first-child {
          text-align: center;
          margin-bottom: 1.6rem;

          @include desktop{
            margin-bottom: 3.7rem;
            p{
              padding: 0 17rem;
            }
          }
        }

        &:last-child{
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-top: 4rem;

          >div{
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            width: 100%;
            margin-bottom: 3.3rem;

            @include desktop{
              padding: 0 5rem;
              width: 50%;
              margin-bottom: 0;
            }

            img{
              width: 4rem;
              @include desktop{
                width: 6rem;
              }
            }

            h3{
              padding: 3.4rem 0 0.5rem 0;

              @include desktop{
                padding: 3.4rem 0 1.1rem 0;
              }
            }
          }

          @include desktop{
            flex-direction: initial;
            align-items: initial;
            margin-top: 0;
          }
        }
      }

      h2:nth-of-type(1){
        display: none;
      }

      @include desktop{
        h2:nth-of-type(2){
          display: none;
        }
        h2:nth-of-type(1){
          display: block;
        }
      }
    }

    &-banner{

      width: 100%;
      height: 27.2rem;
      background-image: url("/assets/images/gobelins_banner.jpg");
      background-size: cover;
      background-color: $primary;
      background-position: center;

      display: flex;
      justify-content: center;
      align-items: center;

      img{
        width: 15.2rem;

        @include desktop{
          width: 26rem;
        }
      }

      &.banner-desktop{
        display: none;

        @include desktop{
          display: flex;
          margin-top: 10.5rem;
          height: 45.6rem;
        }
      }

      &.banner-phone{

        @include desktop{
          display: none;
        }
      }
    }
  }

  .app{

    &-content{
      background-color: $secondary;
      width: 100%;
      display: flex;
      flex-direction: column;
      border-radius: $global-radius;
      position: relative;

      @include desktop{
        flex-direction: initial;
        justify-content: flex-start;
        overflow: hidden;
      }

      >div{
        &:nth-of-type(1){
          padding: 3.3rem;
          display: flex;
          flex-direction: column;
          background-color: $primary;
          border-radius: $global-radius $global-radius 0 0;
          position: relative;
          z-index: 10;

          @include desktop{
            width: 34%;
            padding: 4.9rem 6.2rem;
            border-radius: $global-radius;
          }

          *{
            color: $white;
            text-align: center;

            @include desktop{
              text-align: initial;
            }
          }

          >div{
            display: flex;
            padding: 2.8rem 0 0 0;
            flex-direction: column;
            align-items: center;

            @include desktop{
              flex-direction: initial;
              align-items: initial;
              padding: 5.2rem 0 0 0;
            }

            .btn{
              padding: 1.4rem 1.7rem;

              &:nth-of-type(2){
                margin-top: 1.6rem;
              }

              @include desktop{
                padding: 1.9rem 3.3rem;
                &:nth-of-type(2){
                  margin-left: 2rem;
                  margin-top: 0;
                }
              }
            }
          }
        }
        &:nth-of-type(2){
          height: 20.8rem;
          width: 100%;
          background-image: url("/assets/images/annecy_paysages.jpg");
          background-size: cover;
          background-repeat: no-repeat;
          z-index: 0;
          border-radius: 0 0 $global-radius $global-radius;

          @include desktop{
            height: 100%;
            width: 66%;
            position: absolute;
            top: 0;
            right: 0;
            border-radius: $global-radius;
          }
        }
      }
    }
  }

  .annexes{

    display: flex;
    flex-direction: column;
    align-items: center;

    .container{
      flex-direction: column;
      align-items: center;

      >div{
        display: flex;
        margin-top: 2rem;
        flex-direction: column;

        @include desktop{
          flex-direction: initial;
        }
      }
    }

    &-slider{
      width: 100%;

      @include desktop{
        width: 50%;
      }

      .main-carousel{
        position: relative;
        width: 100%;
        height: 100%;

        .carousel-cell {
          width: 100%;
          height: 30rem;
          padding-bottom: 3rem;

          display: flex;
          justify-content: center;

          @include desktop{
            height: 100%;
          }

          img{
            height: 100%;
            border-radius: $global-radius;
          }
        }

        .flickity-button{
          &.flickity-prev-next-button{
            top: inherit;
            bottom: -9rem;

            background-color: $primary;
            display: flex;
            justify-content: center;
            align-items: center;

            svg{
              fill: $white;
              width: 1.3rem;
              position: initial;
            }
          }
          &.previous{
            left: 20%;

            @include desktop{
              left: 40%;
            }
          }
          &.next{
            right: 20%;

            @include desktop{
              right: 40%;
            }
          }
        }
      }
    }

    &-content{
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 12rem;

      @include desktop{
        width: 50%;
        padding: 3.6rem 0 0 8rem;
        display: block;
        margin-top: 0;
      }

      &-sliderCount{
        font-size: 2.8rem;
        display: flex;
        align-items: flex-start;
        margin-bottom: 2rem;

        &-slide{
          font-size: 4.5rem;
          font-weight: 800;
          margin-right: 1rem;
        }

        &-total{
          margin-top: 0.5rem;
          font-size: 2.3rem;
        }
      }

      h3{
        margin-bottom: 2.3rem;
      }

      p{
        line-height: 3.5rem;
        margin-bottom: 2.9rem;
        text-align: center;

        @include desktop{
          text-align: initial;
        }
      }
    }
  }
}