footer{
  background-color: $primary;
  margin-top: 10rem;
  border-radius: $global-radius $global-radius 0 0;

  @include desktop{
    border-radius: 0;
  }

  .footer-content{
    width: 100%;

    &-main{
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;

      @include desktop{
        margin-top: 5rem;
        flex-direction: initial;
        align-items: flex-start;
      }

      :first-child{
        display: flex;
        flex-direction: column;

        @include desktop{
          justify-content: flex-end;
        }

        h2{
          text-align: center;

          @include desktop{
            text-align: end;
          }
        }

        li{
          text-align: center;

          @include desktop{
            text-align: end;
          }
        }
      }

      h2{
        font-size: 2.4rem;
        font-family: $font-main;
        font-weight: 800;
        color: $secondary;
        letter-spacing: 0rem;
        margin-bottom: 3.4rem;

        @include desktop{
          margin-top: 0;
          text-transform: uppercase;
          font-size: 2.6rem;
          margin-bottom: 1.8rem;
        }
      }

      li{
        &:not(:last-child){
          margin-bottom: 2.6rem;

          @include desktop{
            margin-bottom: 0.7rem;
          }
        }

        &:last-child{
          margin-bottom: 4.6rem;
          @include desktop{
            margin-bottom: 0;
          }
        }
      }

      &-img{
        display: flex;
        justify-content: center;
        align-items: center;

        order: -1;

        margin-bottom: 5.2rem;

        @include desktop{
          margin: 0 12.6rem;
          order: initial;
        }

        img{
          max-width: 20rem;

          @include desktop{
            margin-top: 0.8rem;
          }
        }
      }

      *{
        text-align: center;

        @include desktop{
          text-align: initial;
        }
      }
    }

    &-admin{

      display: flex;
      justify-content: center;

      @include desktop{
        margin-top: 7rem;
      }

      ul{
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

        @include desktop{
          flex-direction: initial;
          align-items: initial;
          text-align: initial;
        }

        li{
          span{
            display: none;

            @include desktop{
              display: inline;
            }
          }
        }
      }
    }
  }
}