html{
  overflow-x: hidden;
  font-size: $font-size;
  background-color: $background;
  body{
    color: $primary;
    font-family: $font-main;
    font-weight: 400;
    overflow-x: hidden;
    position: relative;
    min-height: 101vh;
    background: $background;

    section, footer{
      padding: 4rem 0;
      box-sizing: border-box;
      @include desktop{
        padding: 5rem 0;
      }
    }

    header{
      padding: 5rem 0;
      box-sizing: border-box;
    }

    @include desktop{
      header{
        margin-bottom: 5rem;
      }
    }

    .container{
      max-width: 192rem;
      padding: 0 4rem;
      display: flex;
      height: 100%;

      @include desktop{
        padding: 0 11rem;
      }
    }
  }
}

h1, h2{
  font-size: 3.3rem;
  font-family: $font-title;
  color: $secondary;
  margin-bottom: 4.2rem;
  letter-spacing: .1rem;

  @include desktop{
    font-size: 5.5rem;
    margin-bottom: 5.1rem;
  }
}

h3{
  font-size: 1.6rem;
  font-family: $font-main;
  font-weight: 800;
  color: $primary;
  letter-spacing: 0rem;
  text-transform: uppercase;

  @include desktop{
    font-size: 2.1rem;
  }
}

p{
  font-size: 1.4rem;
  color: $primary;
  font-family: $font-main;
  font-weight: 400;
  line-height: 2.5rem;

  &.p-important{
    font-size: 1.6rem;
    line-height: 2.9rem;

    @include desktop{
      font-size: 2.2rem;
    }
  }

  .p-strong{
    font-weight: 800;
    font-style: italic;
  }

  @include desktop{
    line-height: 3.1rem;
    font-size: 1.9rem;
  }
}

li, a{
  font-size: 1.6rem;
  color: $white;
  font-family: $font-main;
  font-weight: 400;
  line-height: 3.1rem;

  text-decoration: none;

  @include desktop{
    font-size: 1.9rem;
  }
}

.a-important{
  text-decoration: underline;
}