.btn{
  color: $white;
  background-color: $secondary;
  text-decoration: none;

  font-size: 1.3rem;
  text-transform: uppercase;
  font-weight: 800;

  padding: 1.3rem 1.6rem;
  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 1rem;

  width: fit-content;

  img{
    margin-right: 1.3rem;
    width: 1.7rem;

    @include desktop{
      margin-right: 0.8rem;
    }
  }

  @include desktop{
    padding: 2.2rem 4.6rem;
    font-size: 1.9rem;
  }
}